.listItems{
    box-sizing: border-box;
    justify-content: left;
}

.listItems ul{
    padding-left: 0;
}

.listItems li {
    position: relative;
    width:auto;
    height: 7vh;
    padding: 3vh;
    list-style-type: none;
    cursor: pointer;
}

.listItems li[weatherindicator=null]{
    background-color: white;
}

.listItems li[weatherindicator=bkn_-ra_d]{
    background-image: linear-gradient(#f7fcff, #fff8de, lightgray);
}
.listItems li[weatherindicator=bkn_-sn_d]{
    background-image: linear-gradient(#f7fcff, #fff8de, lightblue);
}

.listItems li[weatherindicator=bkn_d]{
    background-image: linear-gradient(#f7fcff, #fff8de);
}

.listItems li[weatherindicator=bkn_ra_d]{
    background-image: linear-gradient(#f7fcff, #fff8de, darkgray);
}

.listItems li[weatherindicator=bkn_sn_d]{
    background-image: linear-gradient(#f7fcff, #fff8de, dodgerblue);
}

.listItems li[weatherindicator=bl]{
    background-image: linear-gradient(dodgerblue, #f7fcff, dodgerblue);
}

.listItems li[weatherindicator=fg_d]{
    background-image: linear-gradient(lightgray, #fff8de, lightgray);
}

.listItems li[weatherindicator=ovc]{
    background-image: linear-gradient(white, lightskyblue);
}

.listItems li[weatherindicator=ovc_-ra]{
    background-image: linear-gradient(white, lightgray);
}

.listItems li[weatherindicator=ovc_-sn]{
    background-image: linear-gradient(white, lightblue);
}

.listItems li[weatherindicator=ovc_ra]{
    background-image: linear-gradient(white, darkgray);
}

.listItems li[weatherindicator=ovc_sn]{
    background-image: linear-gradient(white, dodgerblue);
}

.listItems li[weatherindicator=ovc_ts_ra]{
    background-image: linear-gradient(white, lightyellow, dodgerblue);
}

.listItems li[weatherindicator=skc_d]{
    background-image: linear-gradient(#fff8de, #fff8de);
}

.listItems span {
    display: flex;
    justify-content: space-between;
}

.listItems span.dayofWeek {
    font-size: 200%;
}

.listItems span.briefDate {
    font-size: 120%;
    margin-top: -5px;
}

.listItems span.degrees {
    font-size: 160%;
}

.listItems span.icon img{
    margin-left: 20px;
    width: 8vh;
    height: 8vh;
    margin-top: -5px;
}